
.header {
  background-color: rgba(7,17,27,0.5);
  padding: 20px 12px 12px 24px;
  color: #fff;
}
.avatar {
  display: inline-block;
  border-radius: 4px;
}
.businessmen {
  position: relative;
  margin-bottom: 5px;
}
.businessmen-detail {
  display: inline-block;
  vertical-align: top;
  margin-left: 14px;
}
.businessmen-title {
  margin-bottom: 6px;
  margin-top: 5px;
  font-size: 14px;
  line-height: 18px;
  background-image: url("/static/image/header/brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
  padding-left: 35px;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.businessmen-title {
    background-image: url("/static/image/header/brand@3x.png");
}
}
.description {
  margin-bottom: 6px;
}
.support {
  padding-left: 17px;
  height: 12px;
  line-height: 12px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.support.decrease {
  background-image: url("/static/image/header/decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.support.decrease {
    background-image: url("/static/image/header/decrease_1@3x.png");
}
}
.support.discount {
  background-image: url("/static/image/header/discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.support.discount {
    background-image: url("/static/image/header/discount_1@3x.png");
}
}
.support.guarantee {
  background-image: url("/static/image/header/guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.support.guarantee {
    background-image: url("/static/image/header/guarantee_1@3x.png");
}
}
.support.invoice {
  background-image: url("/static/image/header/invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.support.invoice {
    background-image: url("/static/image/header/invoice_1@3x.png");
}
}
.support.special {
  background-image: url("/static/image/header/special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.support.special {
    background-image: url("/static/image/header/special_1@3x.png");
}
}
.support-count {
  position: absolute;
  bottom: 5px;
  right: 12px;
  cursor: pointer;
  padding: 8px;
  height: 12px;
  line-height: 12px;
  border-radius: 12px;
  background-color: rgba(0,0,0,0.2);
}
.bulletin {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
  background-image: url("/static/image/header/bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
  padding-left: 30px;
  height: 24px;
  line-height: 24px;
  background-color: rgba(0,0,0,0.2);
  background-position: 4px 6px;
  border-radius: 4px;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.bulletin {
    background-image: url("/static/image/header/bulletin@3x.png");
}
}
.layer {
  position: fixed;
  z-index: 4;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(7,17,27,0.8);
}
.layer-businessmen-title {
  font-size: 16px;
  text-align: center;
  line-height: 32px;
  margin-top: 18px;
}
.star {
  margin-top: 10px;
}
.layer-section-head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  text-align: center;
  margin: 26px auto 22px auto;
}
.layer-section-head h3 {
  padding-left: 12px;
  padding-right: 12px;
}
.line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-bottom: rgba(255,255,255,0.2) solid 1px;
  position: relative;
  top: -6px;
}
.offers {
  width: 80%;
  margin: 0 auto;
}
.offers li {
  margin-bottom: 12px;
}
.bulletin-para {
  width: 80%;
  margin: 0 auto;
  line-height: 20px;
}
.close {
  border: solid 1px #ccc;
  height: 24px;
  line-height: 24px;
  padding-left: 12px;
  padding-right: 12px;
  position: fixed;
  bottom: 0;
  left: 45%;
  border-radius: 5px;
}


