
@font-face {
  font-family: 'sell-icon';
  src: url("/static/fonts/sell-icon.eot?ppd93c");
  src: url("/static/fonts/sell-icon.eot?ppd93c#iefix") format('embedded-opentype'), url("/static/fonts/sell-icon.ttf?ppd93c") format('truetype'), url("/static/fonts/sell-icon.woff?ppd93c") format('woff'), url("/static/fonts/sell-icon.svg?ppd93c#sell-icon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change ../fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-favorite:before {
  content: "\e903";
}
.icon-keyboard_arrow_right:before {
  content: "\e904";
}
.icon-remove_circle_outline:before {
  content: "\e905";
}
.icon-shopping_cart:before {
  content: "\e906";
}
.icon-thumb_down:before {
  content: "\e907";
}
.icon-thumb_up:before {
  content: "\e908";
}
.fooder {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 44px;
  z-index: 3;
  width: 100%;
  overflow-x: hidden;
  background-color: #f3f5f7;
}
.fooder-enter-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.fooder-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
}
.fooder-enter,
.fooder-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  opacity: 0;
}
.fooder-header {
  background-color: #fff;
  padding-bottom: 18px;
  border-bottom: #ccc solid 1px;
  position: relative;
}
.fooder-header::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}
.back {
  position: fixed;
  z-index: 4;
  left: 0;
  top: 0;
  font-size: 24px;
  color: #fff;
  cursor: pointer;
  display: block;
  padding: 6px;
}
.fooder-image {
  min-width: 100%;
}
.fooder-info-left {
  margin-left: 18px;
  margin-top: 18px;
  float: left;
}
.fooder-name {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 8px;
}
.fooder-info-sale {
  color: #93999f;
}
.fooder-info-sale span {
  margin-right: 12px;
}
.fooder-price {
  margin-top: 14px;
}
.fooder-price-now {
  font-size: 14px;
  color: #f54100;
  font-weight: bold;
  margin-right: 12px;
}
.fooder-price-old {
  text-decoration: line-through;
}
.joincar,
.joincared {
  float: right;
  margin-right: 18px;
  margin-top: 64px;
  height: 24px;
  color: #fff;
  font-size: 14px;
  border: none;
  padding-left: 12px;
  padding-right: 12px;
  border-radius: 8px;
}
.joincar {
  background-color: #00a0dc;
}
.fooder-intro,
.fooder-assess {
  margin-top: 16px;
  padding: 16px;
  background-color: #fff;
  border-top: #ccc solid 1px;
  border-bottom: #ccc solid 1px;
}
.fooder-intro-title {
  font-size: 16px;
  line-height: 32px;
}
.fooder-intro-para {
  line-height: 20px;
  color: #93999f;
}
.assess-item {
  padding-top: 10px;
  padding-bottom: 12px;
  border-bottom: #ccc solid 1px;
}
.assess-time,
.customer {
  display: inline-block;
  width: 49%;
  color: #b7bbbf;
  line-height: 24px;
}
.customer {
  text-align: right;
}
.assess-avatar {
  margin-left: 5px;
  position: relative;
  top: 5px;
}
.assess-para {
  line-height: 16px;
}
.icon-thumb_up {
  color: #00a0dc;
}
.icon-thumb_up,
.icon-thumb_down {
  margin-right: 4px;
  position: relative;
  top: 1px;
}


