
.main-comment {
  background-color: #f3f5f7;
}
.score {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #fff;
  border-bottom: #ccc solid 1px;
  margin-bottom: 10px;
}
.score-score {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 138px;
          flex: 0 0 138px;
  width: 136px;
  text-align: center;
  border-right: #ccc solid 1px;
}
.score-star {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
.score-score-score {
  color: #ffa942;
  font-size: 24px;
  line-height: 28px;
}
.overall {
  font-size: 16px;
  line-height: 24px;
}
.score-para {
  color: #93999f;
  line-height: 20px;
}
.score-star {
  padding-left: 24px;
  line-height: 24px;
  font-size: 14px;
}
.score-star-dt,
.score-star-dd {
  display: inline-block;
}
.score-star-dd .stars,
.score-star-dd em {
  display: inline-block;
  color: #f90;
}
.score-star-dd .star {
  margin-top: 0;
  position: relative;
  top: 2px;
  margin-left: 4px;
  margin-right: 4px;
}
.delivery-time {
  font-weight: bold;
  color: #93999f;
  margin-left: 5px;
}
.comment-area {
  background-color: #fff;
  padding: 6px 18px 0px 18px;
  border-top: #ccc solid 1px;
}
.comment-wrapper {
  overflow: auto;
}
.comment {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 12px;
  padding-bottom: 12px;
  border-bottom: #ccc solid 1px;
}
.customer-avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 34px;
          flex: 0 0 34px;
  width: 34px;
  margin-right: 12px;
  border-radius: 100%;
}
.comment-info {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
.customer-name {
  font-size: 14p;
  line-height: 16px;
}
.comment-created-at {
  text-align: right;
  color: #93999f;
}
.customer-name,
.comment-created-at {
  display: inline-block;
  width: 49%;
}
.comment-info .stars {
  display: inline-block;
}
.comment-info .star {
  margin-top: 0;
  margin-left: 0;
  margin-right: 4px;
}
.costomer-delivery {
  color: #93999f;
  font-weight: normal;
}
.comment-para {
  margin-top: 6px;
}
.recommends {
  margin-top: 6px;
  color: #93999f;
}
.icon-thumb_up {
  color: #00a0dc;
}
.recommend {
  display: inline-block;
  margin-left: 3px;
  margin-right: 3px;
  margin-bottom: 3px;
  padding-left: 6px;
  padding-right: 6px;
  border: #93999f solid 1px;
  height: 16px;
  line-height: 16px;
  border-radius: 3px;
}


