
@font-face {
  font-family: 'sell-icon';
  src: url("/static/fonts/sell-icon.eot?ppd93c");
  src: url("/static/fonts/sell-icon.eot?ppd93c#iefix") format('embedded-opentype'), url("/static/fonts/sell-icon.ttf?ppd93c") format('truetype'), url("/static/fonts/sell-icon.woff?ppd93c") format('woff'), url("/static/fonts/sell-icon.svg?ppd93c#sell-icon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change ../fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-favorite:before {
  content: "\e903";
}
.icon-keyboard_arrow_right:before {
  content: "\e904";
}
.icon-remove_circle_outline:before {
  content: "\e905";
}
.icon-shopping_cart:before {
  content: "\e906";
}
.icon-thumb_down:before {
  content: "\e907";
}
.icon-thumb_up:before {
  content: "\e908";
}
.assess-types {
  margin-top: 6px;
  border-bottom: #ccc solid 1px;
  padding-bottom: 12px;
  width: 100%;
}
.assess-type {
  display: inline-block;
  margin-right: 6px;
}
.chosetype {
  padding-left: 8px;
  padding-right: 8px;
  border: none;
  height: 32px;
  border-radius: 2px;
}
.chosetype-selected {
  background-color: #00a0dc;
  color: #fff;
}
.chosetype-default {
  background-color: #ccecf8;
}


