.navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 34px;
  line-height: 34px;
  text-align: center;
  position: relative;
}
.navbar::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: #ccc solid 1px;
  content: '';
}
.navbar-item {
