
@font-face {
  font-family: 'sell-icon';
  src: url("/static/fonts/sell-icon.eot?ppd93c");
  src: url("/static/fonts/sell-icon.eot?ppd93c#iefix") format('embedded-opentype'), url("/static/fonts/sell-icon.ttf?ppd93c") format('truetype'), url("/static/fonts/sell-icon.woff?ppd93c") format('woff'), url("/static/fonts/sell-icon.svg?ppd93c#sell-icon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change ../fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-favorite:before {
  content: "\e903";
}
.icon-keyboard_arrow_right:before {
  content: "\e904";
}
.icon-remove_circle_outline:before {
  content: "\e905";
}
.icon-shopping_cart:before {
  content: "\e906";
}
.icon-thumb_down:before {
  content: "\e907";
}
.icon-thumb_up:before {
  content: "\e908";
}
.main-businessmen {
  background-color: #f3f5f7;
  overflow-y: auto;
}
.businessmen-info,
.businessmen-bulletin,
.businessmen-view,
.businessmen-intro {
  padding: 14px 18px;
  background-color: #fff;
  border-bottom: #ccc solid 1px;
  margin-bottom: 16px;
  font-family: '微软雅黑';
}
.businessmen-info-top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: #ccc solid 1px;
  padding-bottom: 14px;
}
.businessmen-info-name-star {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
.businessmen-info-name-star .stars {
  display: inline-block;
}
.businessmen-info-name-star .stars .star:first-of-type {
  margin-left: 0;
}
.businessmen-info-name {
  font-size: 14px;
}
.rating-count,
.sell-count {
  color: #4d555d;
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  margin-right: 12px;
}
.collect-area {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 60px;
          flex: 0 0 60px;
  width: 69px;
  text-align: right;
  color: #4d555d;
}
.collect {
  font-size: 24px;
  display: block;
  margin-bottom: 4px;
  cursor: pointer;
}
.collect-after {
  color: #f54100;
}
.businessmen-info-others {
  padding-top: 14px;
  text-align: center;
}
.businessmen-info-others::after {
  content: '';
  clear: both;
  display: block;
}
.businessmen-info-other {
  float: left;
  width: 33.1%;
  font-size: 14px;
  border-right: #ccc solid 1px;
}
.businessmen-info-other dt {
  color: #b4b8bb;
  margin-bottom: 4px;
  width: 100%;
}
.businessmen-info-other dd {
  color: #07111b;
}
.businessmen-info-other em {
  font-size: 18px;
  font-weight: normal;
}
.businessmen-info-other:last-of-type {
  border-right: none;
}
.businessmen-bulletin-title {
  font-size: 16px;
  color: #07111b;
  line-height: 28px;
}
.businessmen-bulletin-para {
  font-size: 14px;
  line-height: 22px;
  color: #555;
}
.businessmen-supports {
  margin-top: 14px;
  font-size: 14px;
}
.businessmen-support {
  padding-top: 14px;
  padding-bottom: 14px;
  border-top: #ccc solid 1px;
  background-repeat: no-repeat;
  background-position: center left;
  background-size: 24px;
  padding-left: 32px;
}
.decrease {
  background-image: url("/static/image/businessmen/decrease_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.decrease {
    background-image: url("/static/image/businessmen/decrease_3@3x.png");
}
}
.discount {
  background-image: url("/static/image/good/discount_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.discount {
    background-image: url("/static/image/good/discount_3@3x.png");
}
}
.guarantee {
  background-image: url("/static/image/good/guarantee_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.guarantee {
    background-image: url("/static/image/good/guarantee_3@3x.png");
}
}
.invoice {
  background-image: url("/static/image/good/invoice_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.invoice {
    background-image: url("/static/image/good/invoice_3@3x.png");
}
}
.special {
  background-image: url("/static/image/good/special_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.special {
    background-image: url("/static/image/good/special_3@3x.png");
}
}
.view-wrapper {
  margin-top: 12px;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
}
.view {
  display: inline-block;
  margin-right: 12px;
}
.businessmen-intro-para {
  font-size: 14px;
  border-bottom: #ccc solid 1px;
  padding-top: 14px;
  padding-bottom: 14px;
  line-height: 18px;
}


